import {
		mapState
	} from 'vuex';
import md5 from '../../kayida/common/md5.js'

export default {
	computed: {
	            ...mapState(['userInfo'])  
	        },
	data() {
		return {
			id:'',
			pic:'../../static/main/uploading.png',
			pic_m:0,
			task_tips:'',
			popup:0
		}
	},
	methods: {
		uploading() {
			uni.chooseImage({
				// count: 1, 
				// sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				// sourceType: ['album'], //从相册选择
				// success: function(res) {
				// 	console.log(JSON.stringify(res.tempFilePaths));
				// 	console.log(res.tempFiles[0].path)
				// 	this.pic_m = 1
				// 	this.pic = JSON.stringify(res.tempFilePaths)
				// 	this.popup = 0
				// }.bind(this)
				success: (chooseImageRes) => {
					const tempFilePaths = chooseImageRes.tempFilePaths
					this.pic = tempFilePaths[0]
					this.pic_m = 1
					this.popup = 0
				}
			});
		},
		open() {
			this.popup = 1
		},
		canceldown() {
			this.popup = 0
		},
		uploadingSubmit() {
			if(this.pic_m === 0){
				uni.showToast({
					title:"请上传图片",
					icon:'none'
				})
			}else{
				let data = {
					id:this.id,
					task_image:this.pic,
					user_id: this.userInfo.user_id,
					token: this.userInfo.token,
					sign: md5(this.userInfo.user_id+this.userInfo.token)
				}
				this.request.httpRequest('api/task/submit',data,'GET').then(res => {
					uni.showToast({
						title:res.data.msg,
						icon:'none'
					})
				})
			}
		}
	},
	onLoad(option) {
		this.id = option.id
		this.request.httpRequest('api/task/tips','GET').then(res => {
			this.task_tips = res.data.retval.content
		})
	}
}
